 state - from electricity bills to housing registries. Each of these digital databases are suitable for wide-scale use to store and retrieve information with ease. ")]),_c('div',[_c('b-img',{staticStyle:{"width":"80%"},attrs:{"center":"","src":require("../../public/pimg3.png"),"fluid":"","alt":"census data to datasets"}})],1),_c('p',[_vm._v(" The ADMS documented in this project reflect a number of diverse databases and databasing practices being used. Some of these demonstrate clear historical antecedents – such as the various crime and criminal databases at state and central levels. Other databases demonstrate techniques of databasing which are specific to newer technologies of data capture and analysis – such as real-time sentiment analysis on social media, or analytics used in facial recognition systems. ")]),_c('p',[_vm._v(" Increasingly, the kinds of data that are collected, and the manner in which they are organised are being informed by ADMS, Big Data and AI. Public agencies are progressively using automated tools for the purpose of capturing and storing data within databases. Technologies like ‘sentiment analysis’, or social media surveillance technologies, crawl through vast amounts of data on the internet, and only capture and store specific information as determined by the software,"),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote3sym","name":"sdfootnote3anc"}},[_c('sup',[_vm._v("3")])])]),_vm._v(" and without specific human involvement in each instance. Similarly, public agencies are using internet-enabled sensors within various "),_c('a',{attrs:{"href":"http://smartcities.gov.in/content/innerpage/list-of-projects.php"}},[_vm._v("Smart City projects")]),_vm._v(" to monitor traffic or "),_c('a',{attrs:{"href":"https://government.economictimes.indiatimes.com/news/smart-infra/pune-smart-city-installs-50-environmental-sensors/74284275"}},[_vm._v("environmental")]),_vm._v(" conditions to detect and index information programmatically. These technologies are intended to automatically sense and organise particular patterns of data - therefore delegating the task of data selection itself to algorithmic systems. ")]),_c('p',[_vm._v(" Digitisation has allowed for increasing amounts of ‘data’ to be created and stored in a centralised manner, allowing agencies to both easily generate, record and retrieve information from large databases. This has been a major achievement of e-Governance policies and reforms over the last two decades particularly, with extensive computerisation of government agencies, including the computerisation of data which the state collects about citizens. However, many of these databases carry forward historical legacies of inequities and injustice, often obscuring them by treating them as ‘raw data’ or by formalising and entrenching them as standard categories and classifications."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote4sym","name":"sdfootnote4anc"}},[_c('sup',[_vm._v("4")])])])]),_c('blockquote',{attrs:{"cite":"http://"}},[_vm._v(" many of these databases carry forward historical legacies of inequities and injustice, often obscuring them by treating them as ‘raw data’ or by formalising and entrenching them as standard categories and classifications. ")]),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-13'),expression:"'case-study-13'"}],staticClass:"modal-button"},[_vm._v("[Case Study: CCTNS And The Haunting Legacy of Criminal Tribes]")]),_c('b-modal',{attrs:{"id":"case-study-13","size":"xl","hide-footer":""}},[_c('div'),_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v("Case Study: CCTNS And The Haunting Legacy of Criminal Tribes")]),_c('p',[_c('strong',[_vm._v("The Government of India’s Crime and Criminal Tracking Network System is an ongoing project to build a digital network infrastructure for policing across India.")]),_c('sup',[_c('strong',[_c('a',{attrs:{"href":"#sdfootnote5sym","name":"sdfootnote5an