rs maintained in the Bihar CCTNS, or ‘History Sheeters’ registers maintained in Karnataka. Each of these carry forward explicit continuities with the casteist and racial practices of surveilling and punishing ‘criminal tribes’ and institutionally embedding discrimination, including through the technologies of digital databases.")]),_c('sup',[_c('strong',[_c('a',{attrs:{"href":"#sdfootnote6sym","name":"sdfootnote6anc"}},[_c('sup',[_vm._v("6")])])])])]),_c('p',[_c('strong',[_vm._v("As systems like Automated Facial Recognition and predictive analytics are built upon these discriminatory categories and databases, we must introspect on what it means to embed these categories into technological infrastructures which assume ‘neutral’ or ‘data-based’ decisions as the basis for police practice, and how we can interrogate and challenge these systems which form the basis of ADMS. ")])])])])],1),_c('p',[_vm._v(" Digital databases need to be organised and structured in particular ways, in order to be usable. This process of structuring databases, classifying the data and identifying the relationships between the data values, is known as data modelling. Many contemporary digital databases are being built as ‘relational databases’, which is a particular model for structuring and retrieving data. Relational Database Management Systems or RDBMS afford for discrete databases to be linked and information to be retrieved across these databases. RDBMS allows databases to be linked through identifying relationships between two or more records with common features. Common elements in databases can therefore be linked for the purpose of querying or for performing operations across multiple disparate databases. More recently, database models like "),_c('a',{attrs:{"href":"https://docs.microsoft.com/en-us/dotnet/architecture/cloud-native/relational-vs-nosql-data"}},[_vm._v("NoSQL")]),_vm._v(" are being utilised to deal with unstructured forms of data, particularly ‘real-time’ data retrieved 