/articleshow/56861950.cms?from=mdr"}},[_vm._v("National Social Registry")]),_vm._v(" - a combination of public databases). These databases are held within government administrations, with citizens themselves given no notification of whether they are included within the databases, or how their information is eventually utilised, including within ADMS.")])]),_c('p',[_c('br'),_c('br')]),_c('blockquote',{attrs:{"cite":"http://"}},[_c('p',[_c('em',[_vm._v(" “This is the whole principle of machine learning. There is a concept called ML. The more data you feed, all the inaccuracies are ironed out. This is how it will be. This is the meaning of big data.”")])])]),_c('p',[_c('em',[_c('strong',[_vm._v(" “This is the whole principle of machine learning. There is a concept called ML. The more data you feed, all the inaccuracies are ironed out. This is how it will be. This is the meaning of big data.”")])])]),_c('p',[_c('strong',[_vm._v("This "),_c('a',{attrs:{"href":"https://www.medianama.com/2020/09/223-telangana-jayesh-ranjan-interview-facial-recognition/"}},[_vm._v("quote")]),_vm._v(" is a response from a government official to the possibilities of failure in the machine learning component of Samagra Vedika. It is indicative of the prevalent view within policy and government administration in India, that uncritically assumes that increasing the volume and variety of data used in public administration can lead to improved and objective decision-making, without looking into how data is created, what assumptions and biases it embodies, and what the limitations of its utility are. ")])]),_c('div',[_c('b-img',{staticStyle:{"width":"80%"},attrs:{"center":"","src":require("../../public/pimg4.png"),"fluid":"","alt":"samagra vedika"}})],1),_c('p',[_c('strong',[_vm._v("The Samagra Vedika system is now being utilised to determine eligibility of residents for welfare schemes, through the use of statistical modelling and ‘predictive analytics’. According to publicly available documents, 