var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('b-tab',{attrs:{"title":"Algorithms and Automated Decision-Making Systems in India"}},[_c('p',[_vm._v(" An algorithm, in its essence, is a set of rules, or a series of steps, to be followed in any method for reaching a particular output from a given starting point. This toolkitdocuments andanalyses computational algorithms – methods and processes followed within computational processing of data to generate outputs, which are ultimately used within Automated Decision Making Systems, to make consequential decisions. While algorithms vary in form and nature, there are some general characteristics of algorithms which can inform the approaches we take towards addressing the concerns of ADMS use in India."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote1sym","name":"sdfootnote1anc"}},[_c('sup',[_vm._v("1")])])])]),_c('blockquote',{attrs:{"cite":"http://"}},[_c('p',[_vm._v(" Algorithmic systems encode particular forms of knowledge and logic, including biases and assumptions about the behaviour of individuals and society. These systems are particularly important to study because these logics become embedded within computational and networked infrastructure which is replicated and operates at scale and speed – affecting large populations and creating systemic changes, often without the foresight or caution to understand and mitigate their potential harmful consequences. ")])]),_c('p',[_vm._v(" Algorithmic systems encode particular forms of knowledge and logic, including biases and assumptions about the behaviour of individuals and society. These systems are particularly important to study because these logics become embedded within computational and networked infrastructure which is replicated and operates at scale and speed – affecting large populations and creating systemic changes, often without the foresight or caution to understand and mitigate their potential harmful consequences. ")]),_c('p',[_vm._v(" The ‘worldview’ of an algorithm is context-specific, inheriting the knowledge and biases of the designers of the technology. Notwithstanding claims of ‘general artificial intelligence’ likening AI to human intelligence, algorithms are limited by the assumptions on the basis of which they have been designed. They operate and identify only on particular representations of digital data, and extract only such meaning from that data as they have been designed to. The failure to understand these limitations has led to concerning forms of ‘technological solutionism’ within institutions of law and governance. ")]),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-15'),expression:"'case-study-15'"}],staticClass:"modal-button"},[_vm._v("[Case Study: Automated Censorship In Online Platforms]")]),_c('b-modal',{attrs:{"id":"case-study-15","size":"xl","hide-footer":""}},[_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v("Case Study: Automated Censorship In Online Platforms")]),_c('p',[_vm._v(" In December, 2018, the Government of India made public draft rules for regulating online platforms, the draft ‘"),_c('a',{attrs:{"href":"https://www.meity.gov.in/writereaddata/files/Draft_Intermediary_Amendment_24122018.pdf"}},[_vm._v("Intermediary Guidelines Rules, 2018")]),_vm._v("’. Among other things, the rules contained one particularly concerning mandate – Rule 3(9) requires all intermediaries to deploy ‘technology based automated tools’ to proactively identify and disable ‘unlawful information’. Besides being vague and over-broad, the rule indicates the increasing proclivity of public agencies to assume that ‘automated tools’ – essentially, algorithmic systems – are capable of performing complex tasks which inherently involve human judgement – including the ability to identify information which may be unlawful. The problem with these assumptions is the failure to recognise the limitations of algorithmic systems operating in complex, human, contexts. ")]),_c('p',[_vm._v(" Determining the legality of information requires understanding the context in which it appears – whether it is intended as a parody, is a quotation, or if its meaning depends on the group that uses it – contexts which algorithmic models today, cannot understand."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote2sym","name":"sdfootnote2anc"}},[_c('sup',[_vm._v("2")])])]),_vm._v(" Instead, most contemporary algorithmic filters merely look for particular phrases, combinations of words or images, and automatically flag or censor any information which matches these – a process known as ‘fingerprinting’. This is not the first time that public agencies have attempted to use algorithmic systems to censor online content. In 2018, the Central Bureau of Investigation requested social media platforms to deploy an automated content identification system called "),_c('a',{attrs:{"href":"https://indianexpress.com/article/india/cbi-surveillance-photodna-microsoft-facebook-youtube-twitter-5516347/"}},[_vm._v("PhotoDNA")]),_vm._v(", which uses fingerprinting technology to identify photographs and disable their access. While fingerprinting can work in specific, limited contexts – for example, in preventing child sexual exploitation images – its use across contexts risks the pre-censorship of legitimate and lawful speech. Ultimately, the failure to recognise the limitations of ‘AI’ tools and automated censorship can lead to unjustified restrictions on important rights, without scientific or legal legitimacy. ")])])])],1),_c('p',[_vm._v(" The application of algorithms requires the "),_c('strong',[_vm._v("definition of a problem")]),_vm._v(", and the specification of the output which is desired in a manner that can be produced by the algorithmic process. This requires a problem to be formalised in terms of measurable and quantifiable inputs and outcomes. For example, in an algorithm where the question posed is ‘determining fraudulent behaviour in taxation’, the formalisation could be a desired output based on a ‘ground truth’ which states that ‘outlying patterns of differences in declared income and expenditure indicate fraudulent behaviour’. Once the problem has been formalised in this manner, a fraud detection algorithm can be designed to study patterns which match the description of the pattern and classify transactions as ‘fraud’ or ‘not fraud’. The problem definition is an aspect of algorithmic modelling which makes human judgements and values most explicit. For example, in an ADMS which decides whether a person is ‘potentially criminal’, requires the designers of an algorithm to determine what qualities of an individual can be mathematically calculated in order to identify their ‘criminality’, which necessarily incorporates subjective judgements about people’s behaviour."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote3sym","name":"sdfootnote3anc"}},[_c('sup',[_vm._v("3")])])])]),_c('p',[_vm._v(" The "),_c('strong',[_vm._v("nature of the algorithm")]),_vm._v(" used indicates the kind of logic that is applied in coming to a particular decision. Algorithms can be grouped according to the general mathematical or statistical approaches they take towards solving a particular problem (or reaching the desired, pre-defined output). Some of the historical examples of early AI used by public agencies were ‘logic based’ expert systems, which were programmes in the form of ‘if/then’ statements, or deterministic paths to follow from a particular input. On the other hand, many contemporary algorithmic models utilise machine learning."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote4sym","name":"sdfootnote4anc"}},[_c('sup',[_vm._v("4")])])])]),_c('p',[_vm._v(" Machine learning is a set of algorithmic systems, which recognises underlying patterns in data to understand correlations between data, or to model and ‘predict’ the behaviour of future instances of data."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote5sym","name":"sdfootnote5anc"}},[_c('sup',[_vm._v("5")])])]),_vm._v(" Machine learning algorithms are some of the most widely used in ADMS today, including the systems documented here. Machine learning systems, in particular, are increasingly popular as methods to sort through vast amounts of information to identify patterns which may not be immediately obvious to humans. Machine learning models learn from historical instances of data that they are ‘trained’ on. Because machine learning merely reproduces and optimises certain relationships from historical data, the uncritical reliance on machine learning techniques to inform decision-making can reproduce systemic biases and structural inequalities present in underlying historical data. ")]),_c('p',[_vm._v(" The nature of the algorithm can impact not only its utility towards solving a particular problem, but also other important elements around the design of ADMS – such as the ability of the system to be scrutinised, or limitations in its ability to factor in important values like non-discrimination or other constraints. For example, decisions which utilise a linear regression model, which maps relationships between independent variables, may be easier to explain to users of the system, than those which utilise multi-layered neural network models, such as those seen in contemporary facial recognition systems, including those widely used by law enforcement in India."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote6sym","name":"sdfootnote6anc"}},[_c('sup',[_vm._v("6")])])]),_vm._v(" In the previous example of an algorithm for determining fraudulent tax payments, a series of legal rules may be encoded into an expert system which determines whether the input data, as processed by the rules provided to it, indicate fraud. Alternatively, in the case of a machine learning system, the algorithm may be ‘trained’ on previously established cases of ‘frauds’ to find patterns and correlations which can be applied to subsequent cases in order to classify them as ‘fraud’ or ‘not fraud’. ")]),_c('p',[_vm._v(" The task of selecting the algorithm to be applied for a particular problem also requires subjective judgements – what kinds of error rates (false positives or false negatives in the output) are acceptable given the constraints of computing power and time? What is a suitable threshold for statistical bias and variance exhibited by a machine learning algorithm? ")]),_c('p',[_vm._v(" A second important component of algorithmic design is "),_c('strong',[_vm._v("data")]),_vm._v(". Computational algorithms operate on specific "),_c('strong',[_vm._v("databases")]),_vm._v(", within which particular data has been structured and organised. Some of the forms of data and databases used in ADMS in India have been discussed here. As discussed previously, for data to be suitable for computation, data must be classified, structured and organised in particular ways. ")]),_c('div',[_c('b-img',{staticStyle:{"width":"100%"},attrs:{"center":"","src":require("../../public/pimg5.png"),"fluid":"","alt":"out of the box ml"}})],1),_c('blockquote',{attrs:{"cite":"http://"}},[_c('p',[_vm._v(" Various kinds of databases can go into the operation of a single ADMS. In a Machine Learning system, one database from which the algorithmic model ‘learns’ is known as a training database. The ‘trained model’ is often evaluated against a different database to judge its efficacy, through a ‘benchmark database’. Finally, the model operates on ‘new’ databases which it has not previously encountered, at the point of deployment. Each of these databases ultimately impacts how an algorithm performs, as well as how it is evaluated or audited. For example, when a Machine Learning process is identified as being ‘accurate’, it is essential to understand the conditions and context in which the algorithm has been tested, and against what kinds of benchmark datasets. ")])]),_c('p',[_vm._v(" Various kinds of databases can go into the operation of a single ADMS. In a Machine Learning system, one database from which the algorithmic model ‘learns’ is known as a training database. The ‘trained model’ is often evaluated against a different database to judge its efficacy, through a ‘benchmark database’. Finally, the model operates on ‘new’ databases which it has not previously encountered, at the point of deployment. Each of these databases ultimately impacts how an algorithm performs, as well as how it is evaluated or audited. For example, when a Machine Learning process is identified as being ‘accurate’, it is essential to understand the conditions and context in which the algorithm has been tested, and against what kinds of benchmark datasets."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote7sym","name":"sdfootnote7anc"}},[_c('sup',[_vm._v("7")])])])]),_c('p',[_vm._v(" Reliance on algorithms for decision-making can reproduce and magnify inaccuracies or biases at scale, particularly when these algorithms are deployed by public agencies which interface with large populations. Even relatively ‘simple’ algorithmic processes are embedded within complex socio-technical systems of actors, institutions, norms, organisations and technical components, making the operation of algorithms ‘on the ground’ difficult to predict, and making responsibility for inaccuracies or problems in the design of the algorithm difficult to understand or study. ")]),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-16'),expression:"'case-study-16'"}],staticClass:"modal-button"},[_vm._v("[Case Study: Algorithmic Assemblages and the Ghosts in India’s Welfare Machine]")]),_c('b-modal',{attrs:{"id":"case-study-16","size":"xl","hide-footer":""}},[_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v(" Case Study: Algorithmic Assemblages and the Ghosts in India’s Welfare Machine ")]),_c('p',[_vm._v(" The "),_c('em',[_vm._v("Aadhaar ")]),_vm._v("Unique ID project of the Government of India is based on the presupposition that digital databases can represent the ground truth of unique individual identities – a necessary element in the projects claims towards removing ‘leakages’ by removing ‘duplicates’ – the title given to potentially fraudulent actors or ‘ghosts’ who siphon off the legitimate claims of welfare beneficiaries. Making digital records ‘unique’ has therefore always been the claim and the purpose of the biometric ID. These claims of uniqueness are made possible by the assemblages of algorithmic systems utilised within the Aadhaar ADMS. ")]),_c('p',[_vm._v(" Biometric algorithmic systems form the core of Aadhaar’s enrolment and authentication mechanism. A device captures fingerprints, iris scans and face images, and encodes it as an apparently unique digital imprint, by extracting particular patterns and signals from the captured biometric features. These patterns are then matched and compared with existing entries in the biometric database, in order to identify similarities according to a pre-defined threshold. Aadhaar claims to ‘authenticate’ individuals by matching the data captured through a biometric device at any point, to the data stored in the central biometric database. While the UIDAI claims that its biometric matching algorithms are highly efficient and accurate, by the government’s own "),_c('a',{attrs:{"href":"https://www.thequint.com/news/india/uidai-ceo-admits-aadhaar-authentication-failure-rate-12"}},[_vm._v("admission")]),_vm._v(", the ‘failure rate’ of the system is as high as 12%, although it is not clear whether this is attributable to how the data is measured, or how it is matched with other entries in the database. ")]),_c('div',[_c('b-img',{staticStyle:{"width":"100%"},attrs:{"center":"","src":require("../../public/pimg6.png"),"fluid":"","alt":"seeding the raw data begun"}})],1),_c('p',[_vm._v(" Another algorithmic component of Aadhaar has been the ‘seeding’ of Aadhaar numbers within multiple other databases, in order to identify and remove duplicates – in a process known as ‘de-duplication’. According to the "),_c('a',{attrs:{"href":"http://www.dematerialisedid.com/PDFs/role_of_biometric_technology_in_aadhaar_jan21_2012.pdf"}},[_vm._v("UIDAI")]),_vm._v(", the authority in charge of Aadhaar, "),_c('em',[_vm._v("“De-duplication is the process of using the Demographic and Biometric data collected from an enrollee to check against rest of the data so as to avoid duplicate enrolments.” ")]),_vm._v("De-duplication algorithms perform ‘inorganic’ seeding by matching names in various welfare beneficiary databases to the Aadhaar numbers database. If the algorithm does not find a match, it is assumed that the beneficiary entry is a ‘ghost’ or a ‘fake’, if there is more than one match, the beneficiary is assumed to be a ‘duplicate’, and consequently, removed from the list of beneficiaries. The output of the algorithmic computation, then, is privileged over the claims of the affected persons, or even the administrative officials on the ground. ")]),_c('p',[_vm._v(" Every stage of Aadhaar’s decision-making process relies on the design of, and values, biases and errors embedded within the algorithms and technologies utilised within Aadhaar – from the sensitivity of the biometric recognition and matching algorithms to biometrics of different demographics, to the error rates of the ‘seeding algorithms’ for de-duplication. Even as these algorithms have repeatedly been shown to be prone to failure and error, government processes continue to rely heavily on their ‘objectivity’ of algorithmic systems, without designing for how these failures can be contested or overturned by affected persons. ")])])])],1),_c('div',{staticClass:"footnotes"},[_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote1anc","name":"sdfootnote1sym"}},[_vm._v("1")]),_vm._v(" Gillespie T, ‘The Relevance of Algorithms’ in Tarleton Gillespie, Pablo J Boczkowski and Kirsten A Foot (eds), Media Technologies (The MIT Press 2014) <http://mitpress.universitypressscholarship.com/view/10.7551/mitpress/9780262525374.001.0001/upso-9780262525374-chapter-9> ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote2anc","name":"sdfootnote2sym"}},[_vm._v("2")]),_vm._v(" Llansó EJ, ‘No Amount of “AI” in Content Moderation Will Solve Filtering’s Prior-Restraint Problem’ (2020) 7 Big Data & Society. ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote3anc","name":"sdfootnote3sym"}},[_vm._v("3")]),_vm._v(" Barocas S and Selbst AD, ‘Big Data’s Disparate Impact’, "),_c('a',{attrs:{"href":"https://papers.ssrn.com/sol3/papers.cfm?abstract_id=2477899#"}},[_vm._v("104 California Law Review 671 (2016)")]),_vm._v(". ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote4anc","name":"sdfootnote4sym"}},[_vm._v("4")]),_vm._v(" Lehr D and Ohm P, ‘Playing with the Data: What Legal Scholars Should Learn About Machine Learning’ 51 UCD L. Rev., 651. ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote5anc","name":"sdfootnote5sym"}},[_vm._v("5")]),_vm._v(" Mitchell, T., ‘Machine Learning’, (McGraw Hill, 1997). ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote6anc","name":"sdfootnote6sym"}},[_vm._v("6")]),_vm._v(" Burrell J, ‘How the Machine “Thinks”: Understanding Opacity in Machine Learning Algorithms’ (2016) 3 Big Data & Society ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote7anc","name":"sdfootnote7sym"}},[_vm._v("7")]),_vm._v(" Buolamwini J and Gebru T, ‘Gender Shades: Intersectional Accuracy Disparities in Commercial Gender Classiﬁcation’, Proceedings of the 1st Conference on Fairness, Accountability and Transparency, PMLR 81:77-91, 2018 ")])])])])}
