var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('b-tab',{attrs:{"title":"Laws, Policies, Actors and Institutions","active":""}},[_c('p',[_vm._v(" To understand the consequences of the use of ADMS and AI, it is imperative to explore the political, legal and institutional context within which its development and deployment is taking place. This section marks out the legal and institutional frameworks within which AI and ADMS are being adopted in India, and the policies and actors which are shaping the manner in which it is developed and deployed. ")]),_c('p',[_c('strong',[_vm._v("The Politics of AI Policy in India")])]),_c('p',[_vm._v(" The use of ‘AI’-based computer systems in supporting government administration and decision-making in India can be traced back to at-least the 1980’s, with the establishment of research centres for AI, like the Centre for the Development of Advanced Computing, or ‘nodal centres’ within the Department of Electronics which developing AI systems for government administration, supported by international development organisations like the UNDP. ")]),_c('p',[_vm._v(" Early examples of ADMS in India include systems like Eklavya, a software system which aided frontline child health workers with making decisions about diagnosis, health risk and future action for healthcare."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote1sym","name":"sdfootnote1anc"}},[_c('sup',[_vm._v("1")])])]),_vm._v(" Similarly, there is documented use of ‘Automated Legal Reasoning Systems’ under the Knowledge Based Computer Systems programme of the Government of India."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote2sym","name":"sdfootnote2anc"}},[_c('sup',[_vm._v("2")])])]),_vm._v(" These systems implemented rule-based and logic-based programmes for decision-making, which were piloted in the fields of income tax, pension and customs. These systems attempted to encode the logic of statutory rules in these fields into programmatic computer languages in order to aid bureaucrats in complex legal and administrative problems. These early examples are indicative of the Government of India’s desire to embed ADMS within government administration to ensure consistency in decision-making, and to assist administrative agencies in navigating and administering complex rule-based systems. ")]),_c('p',[_vm._v(" The contemporary use of ADMS in India is also justified based on their efficiency in solving complex problems in government administration. However, today, ADMS adoption is occurring in a very different technological and political context, and this transformation is critical in understanding the role that ADMS plays in public agencies in India today. ")]),_c('p',[_vm._v(" Developing ‘AI’ and promoting computational data analytics within the government and the private sector alike has recently become a policy priority for the Government of India, as well as various state governments, over the last few years. Various ‘AI’ policies have been released by the Government of India – from the government policy agency NITI Aayog’s "),_c('a',{attrs:{"href":"https://niti.gov.in/national-strategy-artificial-intelligence"}},[_vm._v("National Strategy for AI,")]),_vm._v(" to "),_c('a',{attrs:{"href":"https://www.meity.gov.in/artificial-intelligence-committees-reports"}},[_vm._v("reports of expert committees")]),_vm._v(" constituted by the Ministry of Electronics and IT – which see AI as a transformational technology, and as a crucial ‘factor of production’ for obtaining higher economic growth in the information economy. These policy documents have called for greater development and adoption of ‘AI’ across the private and public sectors, ranging from healthcare to agriculture. According to some policy documents, ‘AI’ is expected to operate as the infrastructure through which a number of applications and information-based tools can be built and used. For example, the Department of Telecommunications has "),_c('a',{attrs:{"href":"https://www.tec.gov.in/pdf/Whatsnew/ARTIFICIAL%20INTELLIGENCE%20-%20INDIAN%20STACK.pd"}},[_vm._v("articulated")]),_vm._v(" a vision for an ‘AI Stack’ – a technological architecture for AI as infrastructure to be developed and used across a number of applications and domains. ")]),_c('p',[_vm._v(" AI development has also been a policy agenda for state and local governments. Telangana, for example, has reflected its proposals to promote and utilise ADMS and AI systems across different industries and uses, in the ‘"),_c('a',{attrs:{"href":"https://it.telangana.gov.in/2020-is-telanganas-year-of-ai/"}},[_vm._v("2020 Year of AI Vision")]),_vm._v("’. Other documents like ‘"),_c('a',{attrs:{"href":"https://indiaai.gov.in/research-reports/tamil-nadu-safe-ethical-artificial-intelligence-policy-2020"}},[_vm._v("Tamil Nadu Safe and ")]),_c('a',{attrs:{"href":"https://indiaai.gov.in/research-reports/tamil-nadu-safe-ethical-artificial-intelligence-policy-2020"}},[_vm._v("Ethical AI Policy")]),_vm._v("’ also indicate a growing recognition of the need to contend with emerging ethical issues arising from the use of AI, including fairness, transparency and accountability. ")]),_c('p',[_vm._v(" The vision of ‘AI’ articulated in these documents is one of a ‘public good’, championed by private firms and companies developing these technologies, and incentivised and legitimised through government policy, investment,and ‘public-private partnerships’. This vision of AI has also influenced key regulatory and policy developments in India, particularly regarding the governance of digital data. Government policy documents, such as "),_c('a',{attrs:{"href":"https://economictimes.indiatimes.com/news/economy/indicators/budget-2019-in-economic-survey-a-terse-message-for-google-facebook/articleshow/70070224.cms?from=mdr"}},[_vm._v("Economic Survey of India of 2018-2019")]),_vm._v(", the "),_c('a',{attrs:{"href":"https://m.economictimes.com/news/economy/policy/draft-ecomm-policy-seeks-to-set-up-regulator-restrict-data-storage/articleshow/76760134.cms"}},[_vm._v("Draft E-Commerce Policy")]),_vm._v(" and the "),_c('a',{attrs:{"href":"https://www.mygov.in/task/share-your-inputs-draft-non-personal-data-governance-framework/"}},[_vm._v("Report of the Committee of Experts on Non-Personal Data")]),_vm._v(", have attempted to reclassify ‘data’ within digital environments as an economic asset, whose value can be ‘unlocked’ or exploited through appropriately channeling them within AI or data analytics systems. ")]),_c('blockquote',{attrs:{"cite":"http://"}},[_vm._v(" The vision of ‘AI’ articulated in these documents is one of a ‘public good’, championed by private firms and companies developing these technologies, and incentivised and legitimised through government policy, investment,and ‘public-private partnerships’. ")]),_c('p',[_vm._v(" The policy focus on spurring innovation through the deployment of government regulation has had a direct influence on legislative policy as well. The Personal Data Protection Bill, in 2019, had carved out "),_c('a',{attrs:{"href":"https://economictimes.indiatimes.com/tech/internet/draft-data-protection-bill-allows-processing-sans-consent-for-security-credit-scores-debt-recovery/articleshow/72460269.cms?from=mdr"}},[_vm._v("specific exemptions")]),_vm._v(" for activities like Credit Scoring and Fraud Detection, which are common use cases for ADMS. Similarly, the PDP Bill also "),_c('a',{attrs:{"href":"https://indianexpress.com/article/opinion/columns/data-and-its-discontents-lok-sabha-personal-data-protection-bill-6197029/"}},[_vm._v("allowed")]),_vm._v(" for the acquisition of any ‘non-personal’ data by the Government, for ‘better targeting’ of services or for the formulation of ‘evidence-based policy’ – once again evidencing attempts to use ‘big data analytics’ within ADMS to make consequential policy and administrative decisions. ")]),_c('p',[_vm._v(" While there has been an increasing recognition of the risks posed by delegating decision-making to automated systems and ‘AI’, particularly on risks to data protection and privacy, policy documents have understood these are primarilyrisks caused by technological failure, which can be allayed by appropriate technical standards (such as anonymisation of data, or through ‘consent management systems’). ")]),_c('p',[_vm._v(" However, even as ‘AI’ policy which emerges from India recognises some risks and harms in the development and deployment of AI,"),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote3sym","name":"sdfootnote3anc"}},[_c('sup',[_vm._v("3")])])]),_vm._v(" there is a disturbing lack of recognition or regulation around the systems currently in use and being deployed. As documented in this toolkit, many decisions consequential to individuals and communities are being delegated to algorithmic systems, varying in sophistication, but posing concerns – of democratic control, justice and self-determination – which are not reflected within policies which encourage the development and deployment of ‘AI’ systems. In fact, even the documented deployment of current AI and Machine Learning systems does not reflect the concerns of data protection or ‘ethical design’ which are envisaged in policy documents at the ministerial level, pointing to the complete absence of a structural framework to ensure accountability of ADMS on the ground, even as their harms are being recognised in policy. Instead, the development of these systems is taking place in a regulatory vacuum, resulting in a situation where important considerations of transparency, accountability and democratic control are not given their due regard."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote4sym","name":"sdfootnote4anc"}},[_c('sup',[_vm._v("4")])])])]),_c('p',[_vm._v(" Taking the gaze away from high-level policy documents on AI released by government agencies, the development of ADMS in India is being shaped, in fact, by a network of public and private actors, norms and institutions, seemingly unguided by the visions of ‘ethics’ or ‘social good’ articulated in policy documents. The case studies below shine a light on actors and institutions responsible for developing and deploying ADMS in India. ")]),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-9'),expression:"'case-study-9'"}],staticClass:"modal-button"},[_vm._v("[Case Study: ADMS in Policing]")]),_c('b-modal',{attrs:{"id":"case-study-9","size":"xl","hide-footer":""}},[_c('div'),_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v("Case Study: ADMS in Policing")]),_c('p',[_vm._v(" Since 2016, the Federation of the Indian Chambers of Commerce and Industry (FICCI), a major industry association in India, has been instituting the ‘"),_c('a',{attrs:{"href":"http://ficci.in/spdocument/23116/FICCI-Compendium-of-Best-Practices-in-sMART-Policing-2019.pdf"}},[_vm._v("Smart Policing Awards")]),_vm._v("’, ostensibly with the intention of promoting practices for the safety and security of Indians. The awardees over the last four years have included Automated Decision Making Systems like the Punjab Artificial Intelligence System (PAIS),Trinetra in Uttar Pradesh, and Automated Facial Recognition and Number-Plate Recognition Systems in Madhya Pradesh, among others. ")]),_c('p',[_vm._v(" Looking over the awardees of ‘Smart Policing’ gives a good indication of the directions in which technology use in policing is heading, and the actors driving this change. Since the Crime and Criminal Tracking Network System (CCTNS) was first projected as the digital infrastructure to enable ‘smarter’ policing in India, there has been a proliferation of data-driven and digital surveillance-based technologies among policing agencies. Government agencies are funnelling substantial resources into the procurement of sophisticated surveillance and crime analytics systems, including social media and internet communications surveillance systems, as well as video surveillance and data analytics systems to be utilised in public spaces including railways and airports, as well as across public spaces in cities. ")]),_c('p',[_vm._v(" ADMS is transforming the face of law enforcement and the criminal justice system. Historical police practices, encoded in the often archaic frameworks in policing regulation like the police manuals or the police acts – are being supplemented or supplanted by ‘data-driven’ decisions through the use of algorithmic systems. These systems are being used to determine who gets policed – evidenced through so-called ‘predictive policing’ systems like the CMAPS used by the Delhi Police to indicate ‘criminal hotspots’, or the ‘sentiment analysis’ software used by Mumbai and Uttar Pradesh Police, which scans social media to ‘alert’ police forces of potential areas of disturbance. ")]),_c('p',[_vm._v(" ADMS is also being used to expand the reach of policing beyond the restrictions imposed by the beat of a constable or the traditional jurisdiction of a police station. ADMS is moving policing from targeted, suspicion-driven policing and surveillance, which is triggered by police procedure and legal rules, to programmatic and ubiquitous surveillance triggered by algorithmic thresholds and logics."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote5sym","name":"sdfootnote5anc"}},[_c('sup',[_vm._v("5")])])])]),_c('p',[_vm._v(" ADMS is transforming the role and function of policing institutions in India, and not always in a positive way. When automated systems make decisions about whom to surveil, police and investigate, it can embroil individuals within a web of surveillance and incarceration, and, in particular, can reinforce systematic failures within a policing system in dire need of reform – including systematic discrimination towards marginalised populations. ")]),_c('p',[_vm._v(" There is very little empirical evidence of how ADMS is impacting law enforcement in India."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote6sym","name":"sdfootnote6anc"}},[_c('sup',[_vm._v("6")])])]),_vm._v(" While private companies and bureaucrats tout the effectiveness and accuracy of these systems, the automated turn in policing has not been scrutinised for its reliability, nor has there been any systematic effort at understanding ADMS use in policing and its impact on civil liberties or community harms. There has also been no systematic attempt at revising police practice or procedure – from policing manuals to forensic practice – to contend with or govern the use of ADMS. ")]),_c('p',[_vm._v(" How can we ensure that ADMS in policing and criminal justice institutions is not used for unjust surveillance and punishment? ")]),_c('blockquote',{attrs:{"cite":"http://"}},[_vm._v(" How can we ensure that ADMS in policing and criminal justice institutions is not used for unjust surveillance and punishment? ")])])])],1),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-10'),expression:"'case-study-10'"}],staticClass:"modal-button"},[_vm._v("[Case Study: ADMS in Smart Cities]")]),_c('b-modal',{attrs:{"id":"case-study-10","size":"xl","hide-footer":""}},[_c('div'),_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v("Case Study: ADMS in Smart Cities")]),_c('p',[_vm._v(" In June, 2015, the Government of India launched the Smart Cities Mission, a project which envisages networked digital technologies at the heart of managing urban spaces and livelihoods. India’s Smart City project aims to create massive, city-wide digital infrastructure which can solve mundane and persistent issues of urban governance – providing basic utilities, ensuring citizen safety, and making cities ‘future proof’. ADMS are now routinely embedded in public infrastructure, responsible for decisions about our lives and livelihoods – from safety and sanitation, to the supply of basic utilities like electricity and water."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote7sym","name":"sdfootnote7anc"}},[_c('sup',[_vm._v("7")])])])]),_c('p',[_vm._v(" Smart City projects are important institutional forces behind the proliferation of ADMS in India. The models of urban governance in the Smart City are quite explicit in their attempts at governance through surveillance and datafication, both of urban environments and residents."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote8sym","name":"sdfootnote8anc"}},[_c('sup',[_vm._v("8")])])]),_vm._v(" Integrated Command and Control Centres, connected by city-wide Closed Circuit TV’s, use ‘intelligent’ algorithmic machine vision tools to identify and alert officials of ‘loitering’ citizens, or allow city police to predict possible violent crimes."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote9sym","name":"sdfootnote9anc"}},[_c('sup',[_vm._v("9")])])]),_vm._v(" In the ‘smart cities’ of Chandigarh, Nagpur and Indore, workers who maintain urban infrastructure are fitted with ‘Human Efficiency Trackers’ which automatically deduct pay if they depart from the work schedules or routes determined by algorithmic systems, not only normalising invasive surveillance of individuals, but also undermining worker agency and channels of negotiation and grievance redress. ")]),_c('p',[_vm._v(" ADMS in Smart Cities are also used to make decisions about the design of urban infrastructures, which are based on digital inputs gathered, for example, from sensored utility networks, or road transportation systems. In Bengaluru, IBM’s ‘smart water’ solutions have attempted to use Big Data analytics to make decisions about water supply infrastructure."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote10sym","name":"sdfootnote10anc"}},[_c('sup',[_vm._v("10")])])]),_vm._v(" Intelligent Traffic Management Systems are used in conjunction with cameras and environmental sensors to determine traffic in cities like Chennai, Bengaluru and New Delhi, and to assist in planning for transport and mobility infrastructure. ")]),_c('p',[_vm._v(" Smart Cities in India are being enabled through the privatisation of urban infrastructure and a departure from democratic participation and modes of governance. ‘Public Private Partnerships’ abound, and the city governance is corporatised as a ‘Special Purpose Vehicle’ – not quite state, neither entirely corporate – ostensibly to remove messy obstacles towards urban development, but in the process privatising essential utilities and shielding actors and institutions from accountability for digital infrastructure. ")]),_c('p',[_vm._v(" The institution of the Smart City may be leading us towards privatised, opaque and exclusionary digital infrastructures, and routine surveillance of urban residents. How do citizens participate in and demand accountability for automated decision-making in our ‘Smart Cities’? ")]),_c('blockquote',{attrs:{"cite":"http://"}},[_c('p',[_vm._v(" How do citizens participate in and demand accountability for automated decision-making in our ‘Smart Cities’? ")])])])])],1),_c('div',{staticClass:"modal-button-container"},[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-11'),expression:"'case-study-11'"}],staticClass:"modal-button"},[_vm._v("[Case Study: ADMS in Welfare Administration]")]),_c('b-modal',{attrs:{"id":"case-study-11","size":"xl","hide-footer":""}},[_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v("Case Study: ADMS in Welfare Administration")]),_c('p',[_vm._v(" In 2018, the Supreme Court of India upheld the constitutionality of the Government of India’s Unique Identification Project, or "),_c('em',[_vm._v("Aadhaar.")]),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote11sym","name":"sdfootnote11anc"}},[_c('sup',[_vm._v("11")])])]),_vm._v("In responding to the argument that the biometric technologies used to authenticate individual claims to welfare entitlements were based on probabilistic techniques, and were inherently exclusionary, the Court held that a technology which afforded access to social welfare could not be invalidated on the grounds of ‘exclusion of a few’. ")]),_c('p',[_c('em',[_vm._v("Aadhaar ")]),_vm._v("laid the foundation for a proliferation of ADMS and algorithmic tools within government welfare schemes – from determining eligibility for housing, to identification for ration and utility subsidies, to determining welfare fraud in government-sponsored credit programmes. As noted by the UN Special Rapporteur on Extreme Poverty, these systems are leading us to a ‘digital welfare dystopia’ - a rights-free zone with no protections or accountability to preserve institutions and ideals of social security."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote12sym","name":"sdfootnote12anc"}},[_c('sup',[_vm._v("12")])])])]),_c('p',[_vm._v(" The algorithmic turn towards the digital welfare state is apparent from programmes like the Government of Telangana’s Samagra Vedika, which uses machine learning to make decisions about eligibility for housing and pension schemes, or the Government of Orissa’s Kalia scheme, in which a private company provided ‘big data analytics’ solutions to purge the Government’s list of welfare beneficiaries, only to be faced with "),_c('a',{attrs:{"href":"https://www.newindianexpress.com/states/odisha/2019/sep/19/number-of-ineligible-kalia-beneficiaries-is-only-32000-not-341-lakh-minister-2035722.html"}},[_vm._v("substantial backlash")]),_vm._v(" from legitimate beneficiaries. ")]),_c('p',[_vm._v(" ADMS systems offer the possibility of efficiency and neutrality in welfare administration, which is a major attraction for resource-starved bureaucracies tasked with a range of administrative functions. However, these systems are being deployed without any critical interrogation regarding their limitations and their consequences. Systems of ‘fraud detection’ and beneficiary eligibility are utilising obscure and uncertain metrics under the guise of ‘AI’ and Big Data to make judgements about individual identities and claims, and ultimately, these technologies may become a barrier to entitlements, instead of a means to access welfare. ")]),_c('blockquote',{attrs:{"cite":"http://"}},[_vm._v(" How do we challenge ADMS that are used to dispossess people of their rights and entitlements? ")])])])],1),_c('div',[_c('b-img',{staticStyle:{"width":"100%"},attrs:{"center":"","src":require("../../public/pimg2.png"),"fluid":"","alt":"till link do us apart"}})],1),_c('h2',[_vm._v("The Regulatory Landscape of ADMS in India")]),_c('p',[_vm._v(" There is no statute or legislative framework which explicitly attempts to regulate ADMS, algorithms, or Artificial Intelligence in India across contexts. However, ADMS regulation can be examined from how the law interfaces with its different components – namely, data and databases, algorithms and computer programmes, and within its sectoral and context-specific applications. ")]),_c('p',[_c('strong',[_vm._v("Regulation of Data and Databases")])]),_c('p',[_vm._v(" One legal framework which is pertinent to ADMS is that around privacy and the protection of personal data – particularly since many consequential ADMS are reliant upon the algorithmic processing of personal information. In India, "),_c('a',{attrs:{"href":"https://cis-india.org/internet-governance/blog/comments-on-the-it-reasonable-security-practices-and-procedures-and-sensitive-personal-data-or-information-rules-2011"}},[_vm._v("Section 43A")]),_vm._v(" of the Information Technology Act, 2000, attempts to regulate the use of data by private entities, and the Sensitive Data and Personal Information Rules, 2011. However, this legal framework does not apply to public agencies and government establishments, and has significant limitations in terms of its scope, the agency it provides to individuals to control personal data, as well as the structural mechanisms it establishes for data protection. While a more comprehensive ‘"),_c('a',{attrs:{"href":"https://www.prsindia.org/billtrack/personal-data-protection-bill-2019"}},[_vm._v("Personal Data Protection Bill, 2019")]),_vm._v("’ has been deliberated by the Government of India, as at the time of writing, it has not been enacted. ")]),_c('p',[_vm._v(" The Supreme Court of India has articulated a fundamental Right to Privacy under the Constitution of India, which explicitly includes the right to informational privacy, self-determination over personal data, as well as agency over intimate decisions."),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote13sym","name":"sdfootnote13anc"}},[_c('sup',[_vm._v("13")])])]),_vm._v(" While there has been no explicit application of the Right to Privacy to automated data processing within ADMS or AI, it is a touchstone to assess and challenge harmful systems. ")]),_c('p',[_c('strong',[_vm._v("Regulation of Algorithms and Data Processing")])]),_c('p',[_vm._v(" ADMS deployed within specific projects or systems are occasionally the subject of specific regulations. For example, the "),_c('a',{attrs:{"href":"http://legislative.gov.in/actsofparliamentfromtheyear/aadhaar-targeted-delivery-financial-and-other-subsidies-benefits-and"}},[_vm._v("Aadhaar Act, 2016")]),_c('em',[_c('a',{attrs:{"href":"http://legislative.gov.in/actsofparliamentfromtheyear/aadhaar-targeted-delivery-financial-and-other-subsidies-benefits-and"}},[_vm._v(",")])]),_vm._v("attempts to regulate the functioning of the Government of India’s Unique Identification project by specifying the standards of softwares and other technologies to be used within the ADMS systems deployed by Aadhaar, such as biometric authentication systems. Similarly, the Security and Exchange Board of India (SEBI) has "),_c('a',{attrs:{"href":"https://www.sebi.gov.in/legal/circulars/may-2019/reporting-for-artificial-intelligence-ai-and-machine-learning-ml-applications-and-systems-offered-and-used-by-mutual-funds_42932.html"}},[_vm._v("auditing")]),_c('a',{attrs:{"href":"https://www.sebi.gov.in/legal/circulars/may-2019/reporting-for-artificial-intelligence-ai-and-machine-learning-ml-applications-and-systems-offered-and-used-by-mutual-funds_42932.html"}},[_vm._v("requirements")]),_vm._v(" for financial actors utilising AI or ML systems. ")]),_c('p',[_vm._v(" However, there are no regulations specific to algorithmic systems within government systems or other consequential decision-making systems, which specify standards or structures for ensuring transparency and accountability for certain broad classes of algorithmic systems (as, for example, under the "),_c('a',{attrs:{"href":"https://www.republique-numerique.fr/pages/in-english"}},[_vm._v("French Digital Republic Act")]),_vm._v("). Similarly, there are no norms around how to audit or investigate algorithmic systems used within public or private agencies, which is a necessary starting point for determining whether a system is functioning as it should, and what kinds of assumptions or biases an algorithmic system may embody. ")]),_c('p',[_vm._v(" Legal systems around the world are increasingly recognising algorithmic systems as sites of regulation, to ensure that they abide by important rights and values within those jurisdictions. For example, the General Data Protection Regulation (GDPR) in the European Union recognises the need to "),_c('a',{attrs:{"href":"https://ec.europa.eu/newsroom/article29/item-detail.cfm?item_id=612053"}},[_vm._v("provide protections against decisions made by automated systems")]),_vm._v(" which have legal consequences, including the right to have human involvement in such decisions, as well as to demand explanations for such decisions. Similarly, governments are mooting approaches towards structural regulation of consequential algorithmic systems. "),_c('a',{attrs:{"href":"https://www.congress.gov/bill/116th-congress/house-bill/2231/all-info"}},[_vm._v("The Algorithmic Accountability Act")]),_vm._v(" introduced in the USA, for example, attempts to regulate the use of certain algorithmic systems through mandatory reporting, audits and impact assessments. ")]),_c('p',[_c('strong',[_vm._v("Context-Specific ")]),_c('strong',[_vm._v("Regulations on Use of ADMS")])]),_c('p',[_vm._v(" In many implementations of ADMS, its regulation will depend on the specific legal and institutional context within which it is deployed. For example, elements of the Code of Criminal Procedure, State Police Manuals and laws which regulate police action in general, will determine how ADMS is used or regulated in police forces; similarly, principles guiding the conduct of elections, located within laws like the Representation of People’s Act, will regulate the use of ADMS within those contexts. However, even within these specific contexts where ADMS is widely deployed, there is no translation of broad legal principles into regulatory practice, for example, through rules or regulations around the use or deployments of ADMS. It will be necessary to build on sector-specific and context-specific regulatory frameworks to incorporate necessary protections against ADMS. ")]),_c('p',[_vm._v(" There is no single regulatory solution for harmful uses of ADMS in India. However, one important starting point should be to advocate for a strong data protection and private legislation which can incorporate procedural protections against ADMS. India’s long-awaited privacy legislation – the "),_c('a',{attrs:{"href":"http://164.100.47.4/BillsTexts/LSBillTexts/Asintroduced/373_2019_LS_Eng.pdf"}},[_c('u',[_vm._v("Personal Data Protection Bill, 2019")])]),_vm._v(" – is currently being deliberated by members of a Joint Committee of the Houses of Parliament. The Committee has its work cut out for it – the PDP Bill, while progressive on many fronts, suffers from several lacunae and needs to be future-proofed. One aspect that the PDP Bill must account for is whether it is sufficient for an era of ‘Artificial Intelligence’ and ‘Big Data’, where personal data is used to predict and control the behavior of individuals. ")]),_c('div',[_c('b-button',{directives:[{name:"b-modal",rawName:"v-b-modal",value:('case-study-12'),expression:"'case-study-12'"}],staticClass:"modal-button"},[_vm._v("[Case Study: India’s privacy law needs to incorporate rights against the machine]")]),_c('b-modal',{attrs:{"id":"case-study-12","size":"xl","hide-footer":""}},[_c('div'),_c('div',{staticClass:"d-block text-left"},[_c('h3',[_vm._v(" Case Study: India’s privacy law needs to incorporate rights against the machine "),_c('sup',[_c('a',{attrs:{"href":"#sdfootnote14sym","name":"sdfootnote14anc"}},[_c('sup',[_vm._v("14")])])])]),_c('p',[_vm._v(" India’s long-awaited privacy legislation – the"),_c('a',{attrs:{"href":"http://164.100.47.4/BillsTexts/LSBillTexts/Asintroduced/373_2019_LS_Eng.pdf"}},[_c('u',[_vm._v("Personal Data Protection Bill, 2019")])]),_vm._v(" – is currently being deliberated by members of a Joint Committee of the Houses of Parliament. The Committee has its work cut out for it – the PDP Bill, while progressive on many fronts, suffers from several lacunae and needs to be future-proofed. One aspect that the PDP Bill must account for is whether it is sufficient for an era of ‘Artificial Intelligence’ and ‘Big Data’, where personal data is used to predict and control the behavior of individuals. ")]),_c('p',[_vm._v(" Will the PDP Bill curtail the tyranny of the machine? The Bill does, to a large extent, limit the effects of automated decisions, particularly by allowing individuals to control their personal data and its use, as well as structural changes aimed at entities using personal data. In particular, the Bill provides individuals with a (limited) right to access, rectify and erase personal data, which includes inferences for the purpose of profiling. Profiling, in turn, is defined as “any form of processing of personal data that analyses or predicts aspects concerning the behaviour, attributes or interests of a data principal.” Therefore, the Bill takes express cognizance of profiling of individuals by automated processing and to some degree allows individuals to control such profiling. However, despite such recognition, it provides few protections against the specific harms from automated profiling and decision-making, leaving the Data Protection Authority to specify certain ‘additional safeguards’ against profiling for only a subset of personal data deemed to be ‘sensitive’. ")]),_c('p',[_vm._v(" In order to be a robust legislation for our ‘AI’ era, we need to implement expanded protections against automated decisions. One way of extending such protection would be to draw from the legal tradition of ‘"),_c('a',{attrs:{"href":"https://openscholarship.wustl.edu/cgi/viewcontent.cgi?article=1166&context=law_lawreview"}},[_c('u',[_vm._v("due process’")])]),_vm._v(", which ensures that decisions affecting individuals incorporate certain procedural guarantees which are essential to ensuring that they are fair and non-arbitrary. These guarantees include the right to obtain a justification or explanation of decisions, the right to obtain information which was used to make the decision, the right to be heard and have one’s views incorporated in the decision, as well as the right to contest or appeal a decision. In the absence of such protections, legal mechanisms should exist which ensure that individuals have the right to object to automated decisions and to have such decisions be subject to meaningful human oversight. ")]),_c('p',[_vm._v(" However, placing the burden of contesting decisions on affected individuals will not be sufficient. To overcome this burden, data protection law like the PDP Bill could incorporate structural protections to ensure that automated profiling is fair and transparent. These protections may include, for example, regular audits on the data and techniques used in profiling, to ensure its robustness and safeguard against systematic discrimination. Further, the logic or rules of automated processing of data for purposes of proofing must be made transparent by default. Different levels of protection may be offered in different circumstances, according to the potential harm which may be caused to the subject of the decision. ")]),_c('p',[_vm._v(" Opaque and unaccountable AI systems are antithetical to our constitutional ideals of privacy. The Supreme Court of India has"),_c('a',{attrs:{"href":"https://indiankanoon.org/doc/91938676/"}},[_c('u',[_vm._v("noted")])]),_vm._v(" that decisional autonomy – the freedom to make informed choices for oneself – is a core component of the fundamental right to privacy under the constitution. However, AI systems limit our ability to make such informed decisions by classifying and typecasting us according to their own secret rules. As we hurl headfirst into the age of ‘AI’, our legal systems must stand up to the task of protecting our privacy and decisional autonomy. ")]),_c('blockquote',{attrs:{"cite":"http://"}},[_vm._v(" However, AI systems limit our ability to make such informed decisions by classifying and typecasting us according to their own secret rules. As we hurl headfirst into the age of ‘AI’, our legal systems must stand up to the task of protecting our privacy and decisional autonomy. ")])])])],1),_c('div',{staticClass:"footnotes"},[_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote1anc","name":"sdfootnote1sym"}},[_vm._v("1")]),_vm._v(" Chandrasekhara MK, Shanthi B and Mahabala HN, ‘Can Community Health Workers Screen under 5yr Children with Computer Program’ (1994) 61 The Indian Journal of Pediatrics 567 ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote2anc","name":"sdfootnote2sym"}},[_vm._v("2")]),_vm._v(" Bajaj KK, Dubash RK and Kowalski R, ‘Central Government Pension Rules as a Logic Program’ in S Ramani, R Chandrasekar and KSR Anjaneyulu (eds), "),_c('em',[_vm._v("Knowledge Based Computer Systems")]),_vm._v(", vol 444 (Springer-Verlag 1990) <"),_c('a',{attrs:{"href":"http://link.springer.com/10.1007/BFb0018365"}},[_c('u',[_vm._v("http://link.springer.com/10.1007/BFb0018365")])]),_vm._v("> ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote3anc","name":"sdfootnote3sym"}},[_vm._v("3")]),_c('em',[_vm._v("See, for example, ‘")]),_vm._v("Working Document: Enforcement Mechanisms for Responsible #AIforAll’, NITI Ayog, (2020) <https://niti.gov.in/sites/default/files/2020-11/Towards-Responsible-AI-Enforcement-of-Principles.pdf> ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote4anc","name":"sdfootnote4sym"}},[_vm._v("4")]),_vm._v(" Basu, A., Hickok, E., ‘Artificial Intelligence in the Governance Sector in India’, The Centre for Internet and Society, India, <https://cis-india.org/internet-governance/ai-and-governance-case-study-pdf> ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote5anc","name":"sdfootnote5sym"}},[_vm._v("5")]),_vm._v(" Brayne S, ‘Big Data Surveillance: The Case of Policing’ (2017) 82 American Sociological Review 977 ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote6anc","name":"sdfootnote6sym"}},[_vm._v("6")]),_vm._v(" There are progressive efforts to study the use of ADMS in policing, see, for example, Narayan, S, and Marda, V, ‘Data in New Delhi’s Predictive Policing System, Proceedings of the 2020 Conference on Fairness, Accountability, and Transparency, <"),_c('a',{attrs:{"href":"https://dl.acm.org/doi/abs/10.1145/3351095.3372865"}},[_c('u',[_vm._v("https://dl.acm.org/doi/abs/10.1145/3351095.3372865")])]),_vm._v(">, ‘Project Panoptic’, Internet Freedom Foundation, <"),_c('a',{attrs:{"href":"https://internetfreedom.in/tag/project-panoptic"}},[_c('u',[_vm._v("https://internetfreedom.in/tag/project-panoptic")])]),_vm._v(">; Mathews H V, Sinha A., ‘Use of Algorithmic Techniques for Law Enforcement’, 55(23) Economic and Political Weekly (2020). ")])]),_c('div',[_c('p',[_c('a',{attrs:{"href":"#sdfootnote7anc","name":"sdfootnote7sym"}},[_vm._v("7")]),_vm._v(" A snapshot of projects relevant to ADMS in Smart Cities can be seen here - "),_c('a',{attrs:{"href":"http://smar